@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Light.ttf");
  font-weight: 300;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Medium.ttf");
  font-weight: 400;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-SemiBold.ttf");
  font-weight: 600;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-SemiBoldItalic.ttf");
  font-weight: 600;
  font-style: italic;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: Bricolage Grotesque;
  src: url("../../assets/fonts/Bricolage_Grotesque/static/BricolageGrotesque-Bold.ttf");
  font-weight: 700;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button,
input,
textarea,
select {
  font: inherit;
  margin: 0;
  border: none;
  background: transparent;
}

body {
  background-color: #02012C;
  color: #FFFFFF;
}

.container h2 {
  color: #FFFFFF;
  font-family: "DM-Sans";
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 120%;
  letter-spacing: 0;
}
.container--background {
  background-color: #262540;
}

.container-flex {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.btn {
  background-color: #262540;
  border-radius: 0.4rem;
  display: flex;
  align-items: center;
  color: #FFFFFF;
  @apply py-2 px-2.5;
  font-family: "DM-Sans";
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 120%;
  letter-spacing: 0;
}
.btn--light {
  background-color: #3C3B5E;
  @apply py-2 px-4 gap-3;
  font-family: "DM-Sans";
  font-weight: 400;
  font-size: 1rem;
  line-height: 120%;
  letter-spacing: 0;
}

.btn-search {
  border-radius: 1.2rem;
  color: #D4D3D9;
  @apply py-4 px-8 gap-4;
  font-family: "DM-Sans";
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 120%;
  letter-spacing: 0;
}

.btn-search_blue {
  background-color: #4658D9;
  color: #FFFFFF;
  justify-content: center;
}

.dropdown-icon {
  width: 1.7rem;
}

.dropdown-content {
  display: none;
}

.card {
  background-color: #262540;
  border: 1px solid #3C3B5E;
  border-radius: 1.2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.card h3 {
  color: #D4D3D9;
  font-family: "DM-Sans";
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 120%;
  letter-spacing: 0;
}
.card h6 {
  font-family: "DM-Sans";
  font-weight: 300;
  font-size: 2rem;
  line-height: 100%;
  letter-spacing: 0;
}
.card--light {
  background-color: #302F4A;
  border: 1px solid #3C3B5E;
}
.card__temperature {
  font-family: "DM-Sans";
  font-weight: 400;
  font-size: 1rem;
  line-height: 120%;
  letter-spacing: 0;
}
.card__temperature--low {
  color: #D4D3D9;
}
.card__hour {
  font-family: "DM-Sans";
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 120%;
  letter-spacing: 0;
}

header {
  font-family: "Bricolage Grotesque";
  font-weight: 700;
  font-size: 3.25rem;
  line-height: 120%;
  letter-spacing: 0;
}

nav {
  height: 2.0625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .logo {
  height: 100%;
}

@tailwind base;
@tailwind components;
@tailwind utilities;